a {
    color: white;
    text-decoration: none;
}

.footer {
    width: 100%;
    background-color: black;
    color: white;
    font-family: 'Raleway', sans-serif;
    padding: 2em 5.5em 0.3em 5.5em;
}

.footerInfo {
    display: flex;
    margin-bottom: 4em;
}

.info {
    display: flex;
    flex-direction: column;
    width: 30%;
    text-align: left;
}

.contents {
    padding: 1em 0;
}

.closure {
    font-size: 0.7em;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.closureItem {
    margin-left: 2em;
}